$width: 92vmin

html, body
  margin: 0
  padding: 0
  text-align: center
  height: 100%
  overflow: hidden

body
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab)
  background-size: 400% 400%
  animation: gradient 15s ease infinite
  font-family: 'Boogaloo', cursive
  font-size: 100%
  position: relative
  *
    -webkit-tap-highlight-color: transparent


.splash
  position: absolute
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  cursor: pointer
  width: 80vmin
  filter: drop-shadow(10px 10px 5px black)
  img
    width: 60vmin
    display: block
    margin: 0 auto

  h3  
    font-size: 10vmin
    margin: 0.2em
  p
    font-size: 5vmin
    margin: 0.2em
  h3,p
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000
    color: white

.puzzle
  height: $width
  left: 50%
  margin: -($width/2)
  position: absolute
  top: 50%
  width: $width
  box-shadow: black 4vmin 4vmin 10px 0px
  display: flex
  flex-direction: column
  border: 1px solid black
  touch-action: none
  user-select: none
  .row
    flex: 1 1 0px
    display: flex
    .cell
      flex: 1 1 0px
      border: 1px solid black
      background: white
      font-size: 0rem
      svg
        font-size: 1rem
      text
        text-anchor: middle
        font-family: monospace
        text-transform: uppercase
        font-size: 0.6em
  .markers
    position: absolute
    background: rgba(255,0,0,0.5)
    touch-action: none
    pointer-events: none

@keyframes gradient
  0%
    background-position: 0% 50%
  50%
    background-position: 100% 50%
  100%
    background-position: 0% 50%
